<template>
  <div class="foodRate">
      <div class="ratingType">
        <span class="positive" :class="{'active':this.selectType === 2}" @click="select(2,$event)">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
        <span class="positive" :class="{'active':this.selectType === 0}" @click="select(0,$event)">{{desc.nagetive}}<span class="count">{{negative.length}}</span></span>
        <span class="negative" :class="{'active':this.selectType === 1}" @click="select(1,$event)">{{desc.positive}}<span class="count">{{positive.length}}</span></span>
      </div>
      <div class="switch">
        <i @click="switch" class="icon-check_circle" :class="{'on':this.contentSwitch === true}"></i><span>只看有内容的评价</span>
      </div>
  </div>
</template>
<script type="text/ecmascript-6">
  const  NAGETIVE = 0;
  const  POSITIVE = 1;
  const  ALL = 2 ;
export default{
  props:{
    ratings:{
    type:Array,
      default(){
        return [];
      }
      },
    selectType:{
      type:Number,
      default:ALL
    },
    contentSwitch:{
      type:Boolean,
      default:true
    },
    desc:{
      type:Object,
      default(){
        return {
          all:"全部",
          nagetive:"不满意",
          positive:"满意"
        }
      }
    }
  },
  methods:{
    select(type,event){
      if(!event._constructed){
        return;
      }
      this.selectType = type;
      this.$dispatch("ratingType.select",type);
    },
    switch(event){
      if(!event._constructed){
        return;
      }
      this.contentSwitch =!this.contentSwitch;
      this.$dispatch("contentSwitch.select",this.contentSwitch);
    }
  },
  computed:{
    positive(){
      return this.ratings.filter((rate) =>{
         return rate.rateType === POSITIVE;
  })
    },
    negative(){
      return this.ratings.filter((rate) =>{
        return rate.rateType === NAGETIVE;
      })
    }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  .foodRate
    margin 0 18px 0
    .ratingType
      padding  18px 0
      font-size 0
      border-1px(rgba(7,17,27,0.1))
      &>span
        display inline-block
        font-size 12px
        padding 8px 12px
        margin-right 8px
        border-radius 1px
        color rgb(77,85,93)
        .count
          font-size 8px
          margin-left 4px
        &.active
           color #fff
        &.positive
          background-color rgba(0,160,220,0.2)
          &.active
            background-color rgb(0,160,220)
        &.negative
          background-color rgba(77,85,93,0.2)
          &.active
            background-color rgb(77,85,93)
    .switch
      padding  12px 0
      color rgb(147,153,159)
      i
       font-size 24px
       vertical-align middle
       margin-right 4px
       &.on
          color #00c850
      span
        font-size 12px
</style>



