<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img :src="seller.avatar" width="64" height="64">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          <span>{{seller.description}}/{{seller.deliveryTime}}分钟送达</span>
        </div>
        <div v-if="seller.supports" class="supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="supports-count" v-if="seller.supports" @click="showDetail">
        <span class="text">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>

    </div>
    <div class="bulletin-wrapper" @click="showDetail">
      <span class="icon"></span><span class="bulletin">{{seller.bulletin}}</span><i
      class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background"><img :src="seller.avatar" width="100%" height="100%"></div>
    <div class="detail" v-show="detailShow" transition="fade">
      <div class="detail-wrapper clearfix">
        <div class="detail-main">
          <h1>{{seller.name}}</h1>
          <div class="star-wrapper">
            <star :size="48" :score="seller.score"></star>
          </div>
          <div class="title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
            <ul v-if="seller.supports" class="supports">
              <li class="support-item" v-for="item in seller.supports">
                <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
                <span class="text">{{seller.supports[$index].description}}</span>
              </li>
            </ul>
          <div class="title">
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
          <div class="bulletin"><div>{{seller.bulletin}}</div></div>
        </div>
      </div>
      <div class="detail-close"><i class="icon-close" @click="closeDetail"></i></div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from "components/star/star.vue";

  export  default{
    props: {
      seller: {
        type: Object
      }
    },
    data(){
      return {
        detailShow: false
      }
    },
    methods: {
      showDetail(){
        this.detailShow = true;
      },
      closeDetail(){
        this.detailShow = false;
      }
    },
    created(){
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'special'];
    },
    components:{
      star
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .header
    position relative
    background rgba(7, 17, 27, 0.5)
    overflow hidden
    .content-wrapper
      padding 24px 12px 18px 24px
      position relative
      font-size 0
      .avatar
        display inline-block
        width 64px
        height 64px
        margin-right 16px
        vertical-align top
        & > img
          border-radius 2px
      .content
        display inline-block
        font-size 12px
        margin 2px 0 0 0
        .title
          margin-bottom 8px
          font-size 0
          .brand
            width 30px
            height 18px
            display inline-block
            vertical-align top
            margin-right 6px
            bg-img('brand')
            background-size 30px 18px
            background-repeat no-repeat
          .name
            font-size 16px
            line-height 18px
            font-weight bold
        .description
          line-height 12px
          font-size 12px
          margin-bottom 10px
        .supports
          margin-bottom 2px
          font-size 0
          .text
            line-height 14px
            font-size 10px
            vertical-align top
          .icon
            display inline-block
            width 12px
            height 12px
            margin-right 4px
            vertical-align top
            background-size 12px 12px
            background-repeat no-repeat
            &.decrease
              bg-img('decrease_1')
            &.discount
              bg-img('discount_1')
            &.guarantee
              bg-img('guarantee_1')
            &.invoice
              bg-img('invoice_1')
            &.special
              bg-img('special_1')
      .supports-count
        display inline-block
        padding 0 8px
        height 24px
        font-size 0
        position absolute
        right 12px
        bottom 15px
        text-align center
        background-color rgba(0, 0, 0, 0.2)
        border-radius 14px
        .text
          margin-right 2px
          font-size 10px
          line-height 24px
        .icon-keyboard_arrow_right
          line-height 24px
          font-size 10px
          vertical-align top
    .bulletin-wrapper
      height 28px
      line-height 28px
      padding 0 12px
      background-color rgba(7, 17, 27, 0.2)
      overflow hidden
      position relative
      text-overflow ellipsis
      white-space nowrap
      .icon
        display inline-block
        vertical-align middle
        width 22px
        height 12px
        margin-right 4px
        bg-img('bulletin')
        background-size 22px 12px
        background-repeat no-repeat
      .icon-keyboard_arrow_right
        position absolute
        font-size 10px
        bottom 9px
        right 8px
    .background
      position absolute
      top 0
      left 0
      right 0
      bottom 0
      z-index -1
      filter blur(10px)
    .detail
      overflow-y scroll
      position fixed
      z-index 100
      top 0
      left 0
      right 0
      bottom 0
      transition all 0.4s
      &.fade-transition
        opacity 1
        background rgba(7, 17, 27, 0.8)
      &.fade-enter,&.fade-leave
        opacity 0
        background rgba(7, 17, 27, 0)
      .detail-wrapper
        min-height 100%

        height auto
        width 100%
        .detail-main
          margin-top 64px
          padding-bottom 64px
          h1
            font-size 16px
            line-height 16px
            font-weight 700
            text-align center
          .star-wrapper
            margin-top 16px
            text-align center
          .title
            margin 28px auto 24px auto
            width 80%
            display flex
            .text
              padding 0 12px
              font-weight 700
              font-size 14px
            .line
              flex 1
              position relative
              border-bottom 1px solid rgba(255,255,255,0.2)
              top -6px
          .supports
            width 80%
            margin 0 auto 28px auto
            font-size 0
            .support-item
              margin 0 12px 12px 12px
              &:last-child
                margin-bottom 0
              .text
                font-size 12px
                font-weight 200
                line-height 16px
              .icon
                vertical-align top
                display inline-block
                width 16px
                height 16px
                margin-right 6px
                background-size 16px 16px
                background-repeat no-repeat
                &.decrease
                  bg-img('decrease_2')
                &.discount
                  bg-img('discount_2')
                &.guarantee
                  bg-img('guarantee_2')
                &.invoice
                  bg-img('invoice_2')
                &.special
                  bg-img('special_2')
          .bulletin
            width 80%
            margin 0 auto
            font-size 12px
            font-weight 200
            line-height 24px
            div
              margin  0 12px
      .detail-close
        position relative
        width 32px
        height 64px
        display block
        font-size 32px
        color rgba(255, 255, 255, 0.5)
        clear both
        margin -64px auto 0 auto
</style>



