<template>
  <div class="food" v-show="showGood" transition="leftIn" v-el:food>
      <div class="food-content">
        <div class="imgContainer" >
          <img :src="food.image">
          <div class="back icon-arrow_lift" @click="back">
          </div>
        </div>
        <div class="tiltle-message pad-18">
          <div class="name">{{food.name}}</div>
          <div class="sellNum"><span>月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span></div>
          <div class="priceGroup">
            <div class="left">
              <span class="now"><span>￥</span>{{food.price}}</span>
              <span class="old" v-show="food.oldPrice"><span>￥</span>{{food.oldPrice}}</span>
            </div>
            <div class="cartcontrol-wrapper"><cartcontrol :food="food"></cartcontrol></div>
            <div class="addToCart" v-show="!food.count||food.count===0" @click.stop.prevent="addCount" transition="fade">加入购物车</div>
          </div>
        </div>
        <split></split>
        <div class="food-introduce pad-18">
          <div class="title">商品介绍</div>
          <div class="info">{{food.info}}</div>
        </div>
        <split></split>
        <div class="ratings">
          <h1 class="title">商品评价</h1>
          <food-rate :desc="desc" :select-type="selectType" :content-switch="contentSwitch" :ratings="food.ratings"></food-rate>
          <div class="rating-list">
            <ul v-if="food.ratings && food.ratings.length">
              <li v-for="rating in food.ratings" class="Ratelist" v-show="showLish(rating.rateType,rating.text)">
                <div class="rate-time">{{rating.rateTime}}</div>
                <div class="rate-content"><i :class="{'icon-thumb_up':rating.rateType === 0,'icon-thumb_down':rating.rateType ===1}"></i><span class="text">{{rating.text}}</span></div>
                <div class="avatar">
                  <span class="username">{{rating.username}}</span>
                  <img  :src="rating.avatar">
                </div>
              </li>
            </ul>
            <div v-if="!food.ratings || !food.ratings.length ">
              暂无评论
            </div>
          </div>
        </div>
      </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import Vue from "vue";
  import BScroll from "better-scroll";
  import split from "components/split/split";
  import cartcontrol from "components/cartcontrol/cartcontrol";
  import foodRate from "components/foodRate/foodRate";

  const  POSITIVE = 1;
  const  NAGETIVE = 0;
  const  ALL = 2 ;
export default{
  props:{
    food:{
      type:Object
    }
  },
  data(){
    return{
      showFlag:false,
      selectType:ALL,
      contentSwitch:true,
      desc:{
       all:"全部",
        positive:"推荐",
        nagetive:"吐槽"
      }
    }
  },
  methods:{
    show(){
      this.showFlag = true;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$els.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    back(){
      this.showFlag = false;
    },
    addCount(event){
      if(!event._constructed){
        return
      }
      this.$dispatch("cart.add",event.target);
      Vue.set(this.food,'count',1);
    },
    showLish(type,text){
   if(this.contentSwitch && !text){
      return false;
    }
    if(this.selectType ===ALL){
      return true;
    }else{
      return type === this.selectType;
    }
  }
  },
  computed:{
    showGood(){
      if(!this.showFlag){
        return false;
      }
      else{
        return true;
      }
    }
  },
  components:{
   split,cartcontrol,foodRate
  },
  events:{
    "ratingType.select"(type) {
      this.selectType = type;
      this.$nextTick(() =>{
        this.scroll.refresh();
      })
    },
    "contentSwitch.select"(type) {
      this.contentSwitch = type ;
      this.$nextTick(() =>{
        this.scroll.refresh();
      })
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  .pad-18
    padding 18px
  .food
    position fixed
    z-index: 30
    top 0
    left 0
    width 100%
    bottom 48px
    background-color #fff
    &.leftIn-transition
      transition all 0.7s
      transform translate3d(0,0,0)
    &.leftIn-enter,&.leftIn-leave
      transform translate3d(100%,0,0)
    .imgContainer
      width 100%
      height 0
      padding-top 100%
      position relative
      img
        position absolute
        top 0
        left 0
        height 100%
        width 100%
      .back
        position absolute
        top 10px
        left 0
        font-size 16px
        color #fff
        padding 8px
    .tiltle-message
      position relative
      .name
        font-weight 700
        font-size 14px
        color rgb(7,17,27)
        margin-bottom 8px
      .sellNum
        margin-bottom 18px
        font-size 0
        color rgb(147,153,159)
        span
          font-size 10px
          &:first-child
            margin-right 12px
      .priceGroup
        .left
          font-size 0
          .now
            font-size 14px
            color rgb(240,20,20)
            font-weight 700
            margin-right 12px
          .old
            font-size 10px
            color rgb(147,153,159)
            font-weight 700
            text-decoration line-through
          .now,.old
            span
              font-size 10px
              font-weight normal
        .cartcontrol-wrapper
          position absolute
          bottom 6px
          right 18px
        .addToCart
            position absolute
            display inline-block;
            background-color rgb(0,160,220)
            padding 6px 12px
            box-sizing border-box
            bottom 12px
            right 18px
            font-size 10px
            color #fff
            line-height 12px
            border-radius 24px
            z-index: 10
            &.fade-transition
              transition all 0.2s
              opacity 1
            &.fade-enter,&.fade-leave
              opacity 0
              z-index -1
    .food-introduce
      .title
        font-weight 700
        font-size 14px
        color rgb(7,17,27)
      .info
        padding 6px 8px 0
        font-size 12px
        font-weight 200
        color rgb(77,85,93)
        line-height 24px
    .ratings
      padding 18px 0 0 0
      background-color #fff
      border-1px(#e6e7e8)
      .title
        font-weight 700
        font-size 14px
        color rgb(7,17,27)
        margin-left 18px
    .Ratelist
      margin  0 18px
      padding 16px 0
      position relative
      border-1px(rgba(147,153,159,0.1))
      .rate-time
        font-size 10px
        color rgb(147,153,159)
        line-height 12px
        margin-bottom 6px
      .rate-content
        font-size 0
        i
          font-size 12px
          margin-right 4px
          line-height 24px
          &.icon-thumb_up
            color rgb(0,160,220)
          &.icon-thumb_down
            color rgb(147,153,159)
        .text
          font-size 12px
          line-height 16px
          color rgb(7,17,27)
      .avatar
        position absolute
        top 16px
        right 0
        font-size 0
        .username
          font-size 10px
          line-height 12px
          color rgb(147,153,159)
          vertical-align: top
        img
          width 12px
          height 12px
          margin-left 6px
          border-radius 50%
</style>



