<template>
  <div class="goods">
    <div class="menu-wrapper" v-el:menu-wrapper>
      <ul>
        <li v-for="item in goods" class="menu-item" :class="{'current':currentIndex === $index}"
            @click="selectMenu($index,$event)">
          <span class="text border-1px"><span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}</span>
        </li>
      </ul>
    </div>
    <div class="food-wrapper" v-el:food-wrapper>
      <ul>
        <li v-for="item in goods" class="menu-item-title food-list-hook">
          <h1 class="title border-2px">{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" class="no-title-li border-1px" @click="selectGood(food,$event)">
              <div class="li-wrapper">
                <img :src="food.icon" width="57" height="57">
              </div>
              <div class="food-text-item">
                <h2>{{food.name}}</h2>
                <span class="description" v-show="food.description">{{food.description}}</span><br>
                <span class="sell-number">月售{{food.sellCount}}份</span><span class="ratings">好评率{{food.rating}}%</span>
                <div class="price-wrapper">
                  <span class="price">¥ {{food.price}}</span><span class="old-price" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food"></cartcontrol>
                  </div>
                </div>
              </div>
            </li>
          </ul>

        </li>
      </ul>
    </div>
    <shopcart :min-price="seller.minPrice" :delivery-price="seller.deliveryPrice" :select-foods="selectFoods"
              v-ref:shopcart></shopcart>
  </div>
  <food :food="selectedFood" v-ref:food></food>
</template>
<script type="text/ecmascript-6">
  import BScroll from "better-scroll";
  import shopcart from "../shopcart/shopcart";//底部结算组件
  import cartcontrol from "../cartcontrol/cartcontrol"; //增减购物车数量组件
  import food from "../food/food";

  const ERR_OK = 0;
  export default{
    props: {
      seller: {
        type: Object
      }
    },
    data(){
      return {
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood:{}
      }
    },
    components: {
      "shopcart": shopcart,
      "cartcontrol": cartcontrol,
      "food":food
    }
    ,
    computed: {
      //创建计算特性检测
      currentIndex(){
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            //如果不超出数组的范围和在规定的区间内，则返回index的对应索引
            return i;
          }
        }
        return 0;
      },
      selectFoods(){
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food);
            }
          })
        });
        return foods;
      },
      showGood(){
        //计算是否展示商品详情页面
        if(!this.showFlag){
          return false
        }
        return true;
      }
    },
    created(){
      this.$http.get('/api/goods').then((response) => {
        response = response.body;
      if (response.errno === ERR_OK) {
        this.goods = response.data;
        this.$nextTick(() => {
          this._initScroll();
        this._calculateHeight();
      })
        ;
      }
    })
      ;
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'special'];
    },
    methods: {
      selectMenu(index, event){
        if (!event._constructed) {
          return;
        }//自定义点击事件才这个属性,防止PC端点击出现两次 事件回调
        let foodList = this.$els.foodWrapper.getElementsByClassName("food-list-hook");
        let el = foodList[index];
        this.foodScroll.scrollToElement(el, 300);
      },
      _initScroll(){
        this.menuScroll = new BScroll(this.$els.menuWrapper, {
          //由于better-scroll的影响 点击的默认事件会被default 因此需要设置;
          click: true
        });
        this.foodScroll = new BScroll(this.$els.foodWrapper, {
          probeType: 3,
          //由于better-scroll的影响 点击的默认事件会被default 因此需要设置;
          click: true//购物车按钮点击事件
          //better-scroll提供的接口
        });
        this.foodScroll.on("scroll", (pos) => {
          this.scrollY = Math.abs(Math.round(pos.y));
      })
      },
      _calculateHeight(){
        let foodList = this.$els.foodWrapper.getElementsByClassName('food-list-hook');
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      },
      _drop(target){
        this.$refs.shopcart.drop(target);
      },
      selectGood(food,event){
        //点击食物详情的方法
        if(!event._constructed){
          return;
        }
        this.selectedFood = food;
        this.$refs.food.show();
      }
    },
    events: {
      "cart.add"(target) {
        this.$nextTick(() =>{//优化动画性能，异步执行动画，避免卡顿
          this._drop(target);
        }
      )
        ;

      }
    }

  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  .goods
    position: absolute
    width 100%
    top 176px
    bottom 46px
    display flex
    overflow hidden
    .menu-wrapper
      flex 0 0 80px
      width 80px
      background-color #f3f5f7
      .menu-item
        font-size 0
        width 56px
        height 54px
        padding 0 12px
        display table
        &.current
          position relative
          z-index 10
          margin-top -1px
          background-color #fff
          font-weight 700
          .text
            border-none()
        .text
          display table-cell
          width 56px
          font-size 12px
          color rgb(7, 17, 27)
          vertical-align middle
          line-height 14px
          border-1px(rgba(7, 17, 27, 0.1))
          .icon
            display inline-block
            width 12px
            margin-bottom -1px
            height 12px
            margin-right 2px
            background-size 12px 12px
            background-repeat no-repeat
            &.decrease
              bg-img('decrease_3')
            &.discount
              bg-img('discount_3')
            &.guarantee
              bg-img('guarantee_3')
            &.invoice
              bg-img('invoice_3')
            &.special
              bg-img('special_3')
    .food-wrapper
      flex 1
      color rgb(7, 17, 27)
      width 100%
      .menu-item-title
        font-size 0
        h1
          height 26px
          font-size 12px
          line-height 26px
          text-indent 13px
          color rgb(147, 153, 159)
          border-2px(#d9dde1)
          background-color #f3f5f7
        .no-title-li
          display flex
          margin 18px 18px 0 18px
          border-1px(rgba(7, 17, 27, 0.1))
          &:last-child
            border-no()
          .li-wrapper
            flex 0 0 57px
            padding-bottom 18px
          .food-text-item
            flex 1
            vertical-align top
            margin-left 10px
            h2
              padding 2px 0 8px 0
              font-size 14px
              line-height 14px
              color rgb(7, 17, 27)
            .description, .sell-number, .ratings
              display inline-block
              color rgb(147, 153, 159)
              margin-bottom 8px
              font-size 10px
              line-height 10px
            .description
              line-height 14px
              width 160px
            .sell-number
              margin-right 12px
            .price-wrapper
              margin 8px 0 18px
              position relative
              .price, .old-price
                display inline-block
                font-weight 700
              .old-price
                margin-left 8px
                line-height 24px
                color rgb(147, 153, 159)
                font-size 10px
                vertical-align top
                text-decoration line-through
              .price
                font-size 14px
                color rgb(240, 20, 20)
                font-weight 700
                line-height 24px
              .cartcontrol-wrapper
                position absolute
                top 0
                right 0
</style>



