<template>
  <div class="cartcontrol">
    <div class="decrease " v-show="food.count>0" @click.stop.prevent="decreaseCount" transition="move">
      <span class="inner icon-remove_circle_outline"></span>
    </div>
    <div class="cartCount" v-show="food.count>0">{{food.count}}</div>
    <div class="cartAdd icon-add_circle" @click.stop.prevent="addCount"></div>
  </div>
</template>
<script type="text/ecmascript-6">
  import Vue from "vue";
  export default{
    props: {
      food: {
        type: Object
      }
    },
    methods:{
      addCount(event){
          if(!event._constructed){
            return;
          }
          if(!this.food.count){
            Vue.set(this.food,'count',1);
          }else{
            this.food.count++;
          }
          this.$dispatch("cart.add",event.target);
      },
      decreaseCount(event){
        if(!event._constructed){
          return;
        }if(this.food.count!=0){
          this.food.count--;
        }
      }
    }
  }

</script>

<style lang="stylus" rel="stylesheet/stylus">
  .cartcontrol
    font-size 0
    .decrease
      display inline-block
      padding 0 6px 6px 6px
      transition  all 0.4s linear
      &.move-transition
        //动画完成时 vue给标签添加的类名
        opacity 1
        transform translate3d(0,0,0)
        .inner
          display inline-block
          line-height 24px
          font-size 24px
          color rgb(0, 160, 220)
          transition all 0.4s linear
          transform rotate(0)
      &.move-enter,&.move-leave
        opacity 0
        transform translate3d(24px,0,0)
        .inner
          transform rotate(180deg)
    .cartCount
      display inline-block
      font-size 10px
      color rgb(147, 153, 159)
      line-height 24px
      text-align center
      width 12px
      vertical-align top
    .cartAdd
      display inline-block
      font-size 24px
      line-height 24px
      padding 0 6px 6px 6px
      color rgb(0, 160, 220)
</style>



