<template>
  <div class="shopcart">
    <div class="content" @click="toggleCart">
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo" :class="{'highLight':totalCount>0}">
            <i class="icon-shopping_cart" :class="{'highLight':totalCount>0}"></i>
          </div>
          <div class="num" v-show="totalCount>0">{{totalCount}}</div>
        </div>
        <div class="price" :class="{'highLight':totalPrice>0}">￥{{totalPrice}}</div>
        <div class="desc">另需配送费¥{{deliveryPrice}}元</div>
      </div>
      <div class="content-right">
        <div class="pay" @click.stop.prevent="pay" :class="payState">
          {{payPrice}}
        </div>
      </div>
    </div>
    <div class="ball-container">
      <div transition="drop" v-for="ball in balls" v-show="ball.show" class="ball">
        <div class="inner ball-hook"></div>
      </div>
    </div>
    <div class="cart-list" v-show="showList" transition="toggle">
      <div class="cart-title">
        <h1 class="title">购物车</h1>
        <span class="empty" @click="empty">清空</span>
      </div>
      <div class="cart-content" v-el:list-element>
        <ul>
          <li class="food" v-for="food in selectFoods">
            <span class="food-name">{{food.name}}</span>
            <div class="foodPrice"><span class="moneyLogo">¥</span> {{food.price*food.count}}</div>
            <div class="cartcontrol-wrapper">
              <cartcontrol :food="food"></cartcontrol>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div class="mask" @click="closeMask" v-show="showList" transition="fade"></div>
</template>

<script type="text/ecmascript-6">
  import BScroll from "better-scroll";
  import cartcontrol from "../cartcontrol/cartcontrol" //增减购物车数量组件
  export  default{
    data(){
      return {
        balls: [{
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }, {
          show: false
        }],
        dropBalls: [],
        fold: true
      }
    }
    ,
    props: {
      selectFoods: {
        type: Array//如果type是object或者数组,default就是个方法
        /* default(){
         return [];

         }*/
      },
      deliveryPrice: {
        type: Number,
        default: 0
      },
      minPrice: {
        type: Number,
        default: 0
      }
    },
    methods: {
      drop(el){
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (!ball.show) {
            ball.show = true;//触发动画关键点，置为true
            ball.el = el;
            this.dropBalls.push(ball);
            return;
          }
        }
      },
      toggleCart(){
        if (!this.totalCount) {
          return;
        }
        this.fold = !this.fold;
      },
      pay(){
        if (this.totalPrice < this.minPrice) {
          return;
        }
        alert(`请付${this.totalPrice}元`);
      },
      empty(){
        //清空购物车
        let selectFoods = this.selectFoods;
        for (let i = 0; i < selectFoods.length; i++) {
          selectFoods[i].count = 0;
        }
      },
      closeMask(){
        this.fold = true;
      }
    }
    ,
    computed: {
      totalPrice(){
        //获得一个计算属性，这个属性用来得到用户付的总价
        let total = 0;
        this.selectFoods.forEach((food) => {
          total += food.price * food.count;
        });
        return total;
      },
      totalCount(){
        //获得一个计算属性，这个属性用来得到用户总共购物车有几个商品
        let count = 0;
        this.selectFoods.forEach((food) => {
          count += food.count;
        });
        return count;
      },
      payPrice(){
        if (this.totalCount === 0) {
          return `￥${this.minPrice}起送`;
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice;
          return `还差￥${diff}起送`;
        } else {
          return "去结算";
        }
      },
      payState(){
        if (this.totalPrice < this.minPrice) {
          return "noEnough";
        }
        else {
          return "enough";
        }
      },
      showList(){
        //是否展开购物车
        if (!this.totalCount) {
          this.fold = true;
          return false;
        }
        let show = !this.fold;
        if (show) {
          this.$nextTick(() => {
            if (!this.scroll) {
              this.scroll = new BScroll(this.$els.listElement, {
                click: true //cartcontrol只允许在betterScroll派发的点击事件允许增加和减少
              })
            } else {
              this.scroll.refresh();
            }
          })
        }
        return show;
      }
    },
    transitions: {
      drop: {
        beforeEnter(el) {//动画钩子 参数是做这个动画的DOM对象
          let count = this.balls.length;
          while (count--) {
            let ball = this.balls[count];
            if (ball.show) {
              let rect = ball.el.getBoundingClientRect();
              let x = rect.left - 30;
              let y = -(window.innerHeight - rect.top - 30);
              el.style.display = "";
              el.style.webkitTransform = `translate3d(0,${y}px,0)`;
              el.style.transform = `translate3d(0,${y}px,0)`;
              let inner = el.getElementsByClassName('ball-hook')[0];
              inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
              inner.style.transform = `translate3d(${x}px,0,0)`;

            }
          }
        },
        enter(el) {
          /* eslint-disable no-unused-vars */
          let rf = el.offsetHeight;//重绘，让页面刷新
          this.$nextTick(() => {
            el.style.webkitTransform = 'translate3d(0,0,0)';
            el.style.transform = 'translate3d(0,0,0)';
            let inner = el.getElementsByClassName('ball-hook')[0];
            inner.style.webkitTransform = 'translate3d(0,0,0)';
            inner.style.transform = 'translate3d(0,0,0)';
          })
        },
        afterEnter(el) {
          let ball = this.dropBalls.shift();
          if (ball) {
            ball.show = false;
            el.style.display = 'none';
          }
        }
      }
    },
    components: {
      "cartcontrol": cartcontrol
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  .shopcart
    position fixed
    width 100%
    height 48px
    bottom 0
    z-index 50
    .content
      color rgba(255, 255, 255, 0.4)
      display flex
      background-color #141c27
      font-size 0
      .content-left
        flex 1
        .logo-wrapper
          display inline-block
          position relative
          top -10px
          margin 0 12px
          padding 6px
          width 56px
          height 56px
          box-sizing border-box
          background-color #141c27
          vertical-align top
          border-radius 50%
          .logo
            width 100%
            height 100%
            border-radius 50%
            background-color #2b343d
            text-align center
            &.highLight
              background-color #00a0dc
            .icon-shopping_cart
              color #808589
              font-size 24px
              line-height 44px
              &.highLight
                color #fff
          .num
            position absolute
            top 0
            right 0
            width 24px
            height 16px
            line-height 16px
            background-color rgb(240, 20, 20)
            border-radius 12px
            box-shadow 0 4px 8px 0 rgba(0, 0, 0, 0.4)
            font-size 9px
            font-weight 700
            text-align center
            color #fff
        .price
          display inline-block
          vertical-align top
          margin-top 12px
          line-height 24px
          padding-right 12px
          box-sizing border-box
          border-right 1px solid rgba(255, 255, 255, 0.1)
          font-size 16px
          font-weight 700
          &.highLight
            color #fff
        .desc
          display inline-block
          vertical-align top
          margin 12px 0 0 12px
          line-height 24px
          font-size 10px
      .content-right
        flex 0 0 105px
        width 105px
        background-color #2b343b;
        .pay
          height 48px
          line-height 48px
          text-align center
          font-size 12px
          font-weight 700
          &.notEnough
            background-color #2b343b;
          &.enough
            background-color #008739
            color #fff
    .ball-container
      .ball
        position fixed
        bottom 30px
        left 30px
        z-index 200
        &.drop-transition
          transition all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41)
          .inner
            background-color #00a0dc
            width 16px
            height 16px
            border-radius 50%
            transition all 0.4s linear
    .cart-list
      position absolute
      top 0
      left 0
      width 100%
      z-index -1
      &.toggle-transition
        transition all 0.4s
        transform translate3d(0, -100%, 0)
      &.toggle-enter, &.toggle-leave
        transform translate3d(0, 0, 0)
      .cart-title
        background-color #f4f5f7
        height 40px
        line-height 40px
        padding 0 18px
        border-bottom: 1px solid rgba(7, 17, 27, 0.1)
        .title
          color rgb(7, 17, 27)
          font-size 14px
          font-weight 200
          display inline-block
        .empty
          float right
          font-size 12px
          color rgb(0, 160, 200)
      .cart-content
        padding: 0 18px
        max-height: 217px
        overflow: hidden
        background: #fff
        .food
          position: relative
          padding: 12px 0
          box-sizing: border-box
          border-1px(rgba(7, 17, 27, 0.1))
          .food-name
            line-height: 24px
            font-size: 14px
            color: rgb(7, 17, 27)
          .foodPrice
            position: absolute
            right: 90px
            bottom: 12px
            line-height: 24px
            font-size: 14px
            font-weight: 700
            color: rgb(240, 20, 20)
            .moneyLogo
              font-size 10px
        .cartcontrol-wrapper
          position absolute
          right 0
          top 12px

  .mask
    position fixed
    z-index 40
    width 100%
    height 100%
    backdrop-filter blur(10px)
    top 0
    &.fade-transition
      transition all .7s
      background-color rgba(7, 17, 27, 0.6)
    &.fade-enter, &.fade-leave
      background-color rgba(255, 255, 255, 0)
</style>



